:root {
  /** colors */
  --color-main: #171717;
  --color-light: #fff;
  --color-dark: #000;
  --color-accent: #4434c9;
  --color-secondary: #757575;
  --color-border: rgba(35, 35, 35, 0.16);

  /** fonts */
  --font-main: 18px; /* 18px */
  --font-title-hero: 7.111rem; /* 128px */
  --font-title-hero-orig: 1.333rem; /* 24px */
  --font-title: 3.222rem; /* 58px */

  --font-robot-model: 1.055rem; /* 19px */
  --font-robot-name: 1.222rem; /* 22px */
  --font-robot-model-sm: 0.833rem; /* 15px */
  --font-robot-name-sm: 0.944rem; /* 17px */

  --font-small: 0.777rem; /* 14px */

  --font-item: 1.777rem; /* 32px */

  /* height */

  --height-header: 0;
  --padding-offset: 0;
}

@font-face {
  font-family: "Inter";
  src: url("../fonts/Inter-Regular.woff2") format("woff2");
  font-weight: 400;
  font-display: swap;
  font-style: normal;
}

@font-face {
  font-family: "Inter";
  src: url("../fonts/Inter-Medium.woff2") format("woff2");
  font-weight: 500;
  font-display: swap;
  font-style: normal;
}

@font-face {
  font-family: "Inter";
  src: url("../fonts/Inter-SemiBold.woff2") format("woff2");
  font-weight: 600;
  font-display: swap;
  font-style: normal;
}

@font-face {
  font-family: "BebasNeue";
  src: url("../fonts/BebasNeueBold.woff2") format("woff2");
  font-weight: 700;
  font-display: swap;
  font-style: normal;
}

html {
  box-sizing: border-box;
  font-size: var(--font-main);
  scroll-behavior: smooth;
}

*,
*::before,
*::after {
  box-sizing: inherit;
  margin: 0;
  padding: 0;
}

a {
  color: inherit;
  text-decoration: none;
}

img {
  max-width: 100%;
  object-fit: cover;
}

body {
  font-family: "Inter";
  font-weight: 400;
  letter-spacing: -0.04em;
  color: var(--color-main);
}

.page-body {
  overflow-x: hidden;
}

.container {
  max-width: calc(1240px + 30px);
  width: 100%;
  padding: 0 15px;
  margin: 0 auto;
}

.section {
  padding: 5.5555em 0;
}

