  html {
    /* font-size: 12px; */
  }
}

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  overflow: hidden;
  border: 0;
  clip: rect(0 0 0 0);
}

.sr-only {
  clip: rect(0 0 0 0); /* Это свойство устарело*/
  clip-path: inset(50%);
  height: 1px;
  width: 1px;
  overflow: hidden;
  position: absolute;
  white-space: nowrap;
}

.grid {
  display: grid;
}

.gap-20 {
  grid-gap: 1.111em;
}

.grid-col-2 {
  grid-template-columns: repeat(2, 1fr);
}

.grid-col-12 {
  grid-template-columns: repeat(12, 1fr);
}

.block-center {
  display: flex;
  margin: 0 auto;
}

.block-right {
  display: flex;
  flex-direction: column;
  align-items: flex-end;
}

.mb-8 {
  display: block;
  margin-bottom: 8px !important;
}

.flex {
  display: flex;
  align-items: center;
  justify-content: space-between;
}

.stop-scroll {
  overflow: hidden;
}

@media (max-width: 768px) {
  .grid-col-1 {
    grid-template-columns: 1fr;
  }
}

