  letter-spacing: 0.11em;
  color: var(--color-main);
}

.text-small {
  display: block;
  --font-small: 0.777em; /* 14px */
  color: var(--color-main);
  opacity: 0.5;
}

.descr {
  line-height: 1.7777em;
  color: var(--color-secondary);
}

.color-light {
  color: var(--color-light);
}

.color-dark {
  color: var(--color-dark);
}

.dim {
  opacity: 0.5;
}

.nav-list {
  display: flex;
  align-items: center;
}

.stop-scroll {
  position: fixed;
  top: 0;
  left: 0;
  overflow: hidden;
  height: 100vh;
  width: 100%;
}

.nav-item:not(:last-child) {
  margin-right: 2.666rem;
}

.nav-link {
  display: block;
  font-weight: 500;
  line-height: 22px;
  transition: color 0.2s ease-in-out;
}

.nav-link:first-letter {
  text-transform: uppercase;
}

.nav-link:hover {
  color: var(--color-accent);
}

.nav-link:active {
  color: var(--color-secondary);
}

@media (max-width: 992px) {
  .nav.nav--header {
    position: fixed;
    top: 0;
    left: 60%;
    right: 0;
    bottom: 0;
    height: 100%;
    overflow-y: auto;
    background-color: rgba(117, 117, 117, 0.9);
    z-index: 100;
    border-top-left-radius: 20px;
    border-bottom-left-radius: 20px;
    border: 3px solid var(--color-accent);
    border-right: none;
    transform: translateX(100%);
    transition: transform 0.6s ease-in-out;
  }

  .nav--visible {
    transform: translateX(calc(0.1% + var(--padding-offset))) !important;
  }

  .nav.nav--header .nav-list {
    flex-direction: column;
    margin-top: var(--height-header);
  }

  .nav.nav--header .nav-item:not(:last-child) {
    margin-right: 0;
  }

  .nav.nav--header .nav-item {
    padding: 20px 0;
  }

  .nav.nav--header .nav-link {
    color: var(--color-light);
    font-size: 20px;
  }
}

@media (max-width: 576px) {
  .nav.nav--header {
    left: 40%;
  }
}

@media (max-width: 360px) {
  .nav.nav--header {
    left: 0%;
  }
}

.logo {
  display: block;
  max-width: 166px;
  height: 48px;
}

.logo img {
  display: block;
  width: 100%;
  height: 100%;
  object-fit: cover;
}

.profile {
  display: inline-block;
  width: 24px;
  height: 24px;
}

.cart {
  display: inline-block;
  width: 24px;
  height: 24px;
}

.profile,
.cart {
  transition: transform 0.3s ease-in-out;
}

.profile:hover,
.cart:hover {
  transform: scale(1.3);
}

.profile--icon {
  background-image: url("../img/Profile.svg");
}

.cart--icon {
  background-image: url("../img/Cart.svg");
}

.profile--icon,
.cart--icon {
  background-repeat: no-repeat;
  background-size: cover;
  background-position: center;
}

@media (max-width: 360px) {
  .logo {
    max-width: calc(166px / 1.2);
    height: calc(48px / 1.2);
  }
}

.burger {
  display: none;
  position: relative;
  width: 40px;
  height: 10px;
  overflow: hidden;
  cursor: pointer;
}

.burger::before,
.burger::after {
  content: "";
  display: block;
  position: absolute;
  left: 0;
  width: 100%;
  height: 2px;
  background-color: black;
  /* transition: transform 0.6s ease-in-out; */
  transition: background-color 0.6s ease-in-out, transform 0.6s ease-in-out;
}

.burger::before {
  top: 0;
}

.burger::after {
  bottom: 0;
  transform: translateX(50%);
}

.burger--active.burger.burger::after {
  bottom: 0;
  transform: translateX(0%);
}

.burger--active.burger.burger::after,
.burger--active.burger.burger::before {
  background-color: var(--color-light);
}

/* .burger--active.burger::before,
.burger--active.burger::after {
  background-color: var(--color-light);
  transition: background-color 0.3s ease-in-out;
} */

@media (max-width: 992px) {
  .burger {
    position: relative;
    display: block;
    z-index: 100;
  }
}


